import React, { useState, useEffect } from 'react';
import ArticleList from '../components/ArticleList';
import CommentsList from '../components/CommentsList';
import UpvoteSection from '../components/UpvoteSection';
import articles from "./article-content";
import PaheNotFound from './PageNotFound';
import AddCommentsForm from '../components/AddCommentsForm';

function ArticlePage({match}) {
    const name = match.params.name;
    const article = articles.find(article => article.name === name);

    const [ articleInfo, setArticleInfo ] = useState({ upvotes: 0, comments: []});

    useEffect(() => {
        const fetchData = async () => {
            const result = await fetch(`/api/articles/${name}`);
            const body = await result.json();
            setArticleInfo(body);
            {console.log(articleInfo)}
        }
        fetchData();
    }, [name])

    if (!article) return <PaheNotFound />;

    const otherArticles = articles.filter(article => article.name !== name);
    return (
        <>
            <h1>This is an {article.name} article</h1>
            <UpvoteSection articleName={name} upvotes={articleInfo.upvotes} setArticleInfo={setArticleInfo} />
            {article.content.map((paragraph, key) => (
                <p key={key}>{paragraph}</p>
            ))}
            
            <CommentsList comments={articleInfo.comments} />
            <AddCommentsForm articleName={name} setArticleInfo={setArticleInfo} />

            <h3>Other Articles:</h3>
            <ArticleList articles={otherArticles} />

        </>
    )
}

export default ArticlePage;